const globals = () => {
    Chart.defaults.global.hover.mode = 'nearest';
    // Chart.defaults.global.colors = ['#fd7e14', '#ffc107', '#dc3545', '#e83e8c'];
    Chart.defaults.global.defaultFontFamily = "'Muli', 'Helvetica Neue', 'Helvetica', 'Arial', sans-serif";
    Chart.defaults.global.defaultFontSize = 11;
};

const chartjs = () => {
    globals();
    barcharts();
};

const barcharts = () => {
    $(".chart-bar").each(function() {
        let label = $(this).data('label');
        let chartLabels = $(this).data('chart-labels');
        let chartData = $(this).data('chart-data');
        let datasets = [];

        for (var [index, data] of chartData) {
            console.log(index);
            console.log(data);
            datasets[index] = [{
                label: label,
                data: data,
                fill: false,
                borderWidth: 1,
                backgroundColor: "rgba(255, 102, 0, 0.5)",
                borderColor: "rgba(255, 102, 0, 1)"
            }]
        };

        let chart = new Chart($(this)[0], {
            type: 'bar',
            data: {
                labels: chartLabels,
                datasets: datasets
            },
            options: {
                scales: {
                    yAxes: [{
                        ticks: {
                            beginAtZero:true
                        }
                    }]
                }
            }
        });
    });
}

export default chartjs


